import { useState } from 'react'

export default function PriorityInputRange() {
    console.log('render PriorityInputRange')
    const [priority, setPriority] = useState(5)
    const showPriority = function(event) {
        setPriority(event.target.value)
    }
    return (
        <>
        <label htmlFor="Priority_Input" className="form-label">Priority: {priority}</label><br/>
        <input
            type="range"
            id="Priority_Input"
            name="Priority_Input"
            min="0"
            max="10"
            onChange={showPriority}
            value={priority}
            className="form-range"
        />
        <input
            type="range"
            min="0"
            max="10"
        />
        </>
    )
}